import './Right'
import person3 from '../../Assets/Image/person-2.png';

function RightFollow({ name, username}) {
    return (
        <div className="right__account">
            <img src={person3} alt="" className="right__account-img" width="63" />
            <div className="right__account-info">
                <h3 className="right__account-name">{name}</h3>
                <h3 className="right__account-username">{username}</h3>
            </div>
            <button className="right__account-btn">Follow</button>
        </div>
    )
}

export default RightFollow
