import './Login.scss';
import logo from '../Assets/Image/twitter-logo.svg'

function Login() {
    return (
        <form className="form">
            <img src={logo} alt="logo" width="50" height="41" />
            <h1 className="form__title">Log in to Twitter</h1>
            <input className="form__input" type="email" placeholder="Email address"/>
            <input className="form__input" type="password" placeholder="Password" />
            <button className="form__btn">Log In</button>
            <div className="form__links">
                <a href="#">Forgot password?</a>
                <a href="#">Sign up to Twitter</a>
            </div>
        </form>
    )
}

export default Login
