import './Tweets.scss'

// images
import person2 from '../../Assets/Image/person-4.png'
import threedots from '../../Assets/Image/threedots.svg'
import commentIcon from '../../Assets/Image/comment.svg'
import rotationIcon from '../../Assets/Image/rotation.svg'
import likeIcon from '../../Assets/Image/like.svg'
import send from '../../Assets/Image/send.svg'
import statistics from '../../Assets/Image/statistics.svg'

function Tweets({name, username, time, comment, rotation, like}) {
    return (
        <div className="tweets">
            <img className="tweets__img" src={person2} alt="person" width="60"/>
            <div className="tweets__info-wrapper">
                <div className="tweets__info">
                    <h3 className="tweets__name">{name}</h3>
                    <span className="tweets__username">{username}</span>
                    <span className="tweets__dot">.</span>
                    <time className="tweets__time">{time}</time>
                    <button className="tweets__info-btn"><img src={threedots} alt="threedots" /></button>
                </div>
                <p className="tweets__text">Lorem ipsum dolor sit amet, consectetur adipisicing elit. At facilis quam numquam facere accusantium reiciendis.</p>
                <div className="tweets__btns">
                    <div className="tweets__btn-wrapper">
                        <button className="tweets__btn"><img src={commentIcon} alt="comment" /></button>
                        <span className="tweets__btn-amount">{comment}</span>
                    </div>
                    <div className="tweets__btn-wrapper">
                        <button className="tweets__btn"><img src={rotationIcon} alt="rotation" /></button>
                        <span className="tweets__btn-amount">{rotation}</span>
                    </div>
                    <div className="tweets__btn-wrapper">
                        <button className="tweets__btn"><img src={likeIcon} alt="like" /></button>
                        <span className="tweets__btn-amount">{like}</span>
                    </div>
                    <button className="tweets__btn"><img src={send} alt="send" /></button>
                    <button className="tweets__btn"><img src={statistics} alt="statistics" /></button>
                </div>
            </div>
        </div>
    )
}

export default Tweets