import './Nav.scss';
import {NavLink} from 'react-router-dom';

// images
import logo from '../../Assets/Image/twitter-logo.svg';
import homeIcon from '../../Assets/Image/home.svg';
import exploreIcon from '../../Assets/Image/explore.svg';
import notificationsIcon from '../../Assets/Image/notifications.svg';
import messagesIcon from '../../Assets/Image/messages.svg';
import bookmarksIcon from '../../Assets/Image/bookmarks.svg';
import listsIcon from '../../Assets/Image/lists.svg';
import profileIcon from '../../Assets/Image/profile.svg';
import more from '../../Assets/Image/more.svg';
import threedots from '../../Assets/Image/threedots.svg';
import person1 from '../../Assets/Image/person-1.png';

function Nav() {
    return (
        <nav className="nav">
            <img className="nav__logo" src={logo} alt="logo" width="40"/>
            <div className="nav__links">
                <NavLink className="nav__link" to="/"><img src={homeIcon} alt="icon"/>Home</NavLink>
                <NavLink className="nav__link" to="/explore"><img src={exploreIcon} alt="icon"/>Explore</NavLink>
                <NavLink className="nav__link" to="/notifications"><img src={notificationsIcon} alt="icon"/>Notifications</NavLink>
                <NavLink className="nav__link" to="/messages"><img src={messagesIcon} alt="icon"/>Messages</NavLink>
                <NavLink className="nav__link" to="/bookmarks"><img src={bookmarksIcon} alt="icon"/>Bookmarks</NavLink>
                <NavLink className="nav__link" to="/lists"><img src={listsIcon} alt="icon"/>Lists</NavLink>
                <NavLink className="nav__link" to="/profile"><img src={profileIcon} alt="icon"/>Profile</NavLink>
                <button className="nav__more-btn"><img src={more} alt="more btn"/>More</button>
            </div>
            <button className="nav__btn">Tweet</button>
            <div className="nav__account">
                <img src={person1} alt="" className="nav__account-img" width="50" />
                <div className="nav__account-info">
                    <h3 className="nav__account-name">Bobur</h3>
                    <h3 className="nav__account-username">@bobur_mavlonov</h3>
                </div>
                <img src={threedots} alt="account" className="nav__account-img" />
            </div>
        </nav>
    )
}

export default Nav
