import './Home.scss'
import homeStarsIcon from '../Assets/Image/header-stars.svg'

// images
import person1 from '../Assets/Image/person-1.png'
import gallery from '../Assets/Image/gallery.svg'
import gif from '../Assets/Image/gif.svg'
import stats from '../Assets/Image/stats.svg'
import smile from '../Assets/Image/smile.svg'
import schedule from '../Assets/Image/schedule.svg'
import Tweets from '../Components/Tweets/Tweets'

function Home() {
    return (
        <>
            <header className="header">
                <div className="header__top">
                    <h2 className="header__title">Home</h2>
                    <img src={homeStarsIcon} alt="home icon" />
                </div>
                <div className="header__tweet">
                    <div className="header__tweet-top">
                        <img src={person1} alt="profile" width="60"/>
                        <input type="text" placeholder="What is happening?"/>
                    </div>
                    <div className="header__tweet-bottom">
                        <div className="img-wrapper">
                            <img src={gallery} alt="gallery" />
                            <img src={gif} alt="gif" />
                            <img src={stats} alt="stats" />
                            <img src={smile} alt="smile" />
                            <img src={schedule} alt="schedule" />
                        </div>
                        <button type="button" className="header__tweet-btn">Tweet</button>
                    </div>
                </div>
            </header>
            <Tweets name='Designsta' username='@inner' time='25m' comment='10' rotation='10' like='8' />
            <Tweets name='cloutexhibition' username='@inner' time='22m' comment='' rotation='5' like='9' />
            <Tweets name='CreativePhoto' username='@inner' time='1h' comment='10' rotation='1' like='8' />
        </>
    )
}

export default Home