import './Right'
import threedots from '../../Assets/Image/threedots.svg';

function RightTrend({type, title, tweets}) {
    return (
        <div className="right__trend">
            <div className="right__trend-left">
                <span className="right__trend-type">{type}</span>
                <h3 className="right__trend-title">{title}</h3>
                <span className="right__trend-tweets">{tweets}</span>
            </div>
            <button className="right__trend-btn"><img src={threedots} alt="threedots" /></button>
        </div>
    )
}

export default RightTrend