import './Right.scss'
import settingsIcon from '../../Assets/Image/settings.svg'
import searchIcon from '../../Assets/Image/search.svg'
import RightTrend from './RightTrend'
import RightFollow from './RightFollow'

function Right() {
    return (
        <div className="right">
            <div className="right__search-wrapper">
                <input type="text" placeholder="Search Twitter" className="right__search" />
                <img src={searchIcon} alt="search icon" />
            </div>
            <div className="right__trends">
                <div className="right__trends-header">
                    <h2 className="right__trends-title">Trends for you</h2>
                    <img src={settingsIcon} alt="settings icon" />
                </div>
                <RightTrend type='Trending in Germany' title='Revolution' tweets='50.4K Tweets' />
                <RightTrend type='Trending in Germany' title='Revolution' tweets='50.4K Tweets' />
                <RightTrend type='Trending in Germany' title='Revolution' tweets='50.4K Tweets' />
                <button className="right__trends-btn">Show more</button>
            </div>
            <div className="right__follow">
                <h2 className="right__follow-title">You might like</h2>
                <RightFollow name='Shuhratbek' username='@mrshukhrat' />
                <RightFollow name='Shuhratbek' username='@mrshukhrat' />
                <a href="#" className="right__follow-link">Show more</a>
            </div>
            <div className="right__bottom-links">
                <a target="_blank" href="#">Terms of Service</a>
                <a target="_blank" href="#">Privacy Policy</a>
                <a target="_blank" href="#">Cookie Policy</a>
                <a target="_blank" href="#">Imprint </a>
                <a target="_blank" href="#">Ads Info</a>
                <button>More...</button>
                <span> 2021 Twitter, Inc.</span>
            </div>
        </div>
    )
}

export default Right
